<template>
  <div>
      <div class="preview-container" :class="{screen_display: shown}">
          <BookTitle />
          <BookDedication class="pagebreak" />
          <IndexPage class="pagebreak" />
          <div class="page-title-container pagebreak" v-for="chapter of addedChapters" :key="chapter.id">
            <div class="page-container">
                <div class="page">
                    <h3 class="chapter-title" >{{ chapter.title }}</h3>
                    <div class="page-body" v-html="chapter.content"></div>
                </div>
            </div>
          </div>
          <BookBackCover class="pagebreak" />
      </div>
      <button class="close-preview btn btn-primary" :class="{screen_display: shown}" @click="$store.commit('displayPreview', false)">Close</button>
  </div>
</template>

<script>
import BookTitle from "@/components/BookTitle.vue"
import BookDedication from "@/components/BookDedication.vue"
import IndexPage from "@/components/IndexPage.vue"
import BookBackCover from "@/components/BookBackCover.vue"

export default {
    data(){
        return {
            
        }
    },
    components: {
        BookTitle,
        BookDedication,
        IndexPage,
        BookBackCover
    },
    computed: {
        addedChapters: {
            get() {
                return this.$store.state.data.available_chapters.filter(chapter=>{
                    return chapter.added;
                })
            }
        },
        shown(){
            return this.$store.state.data.display_preview
        }
    },
}
</script>

<style lang="scss" scoped>
@media screen {
    .preview-container {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: rgba(0,0,0,0.8);
        overflow-y: scroll;
    }
}


.close-preview {
    position: absolute;
    right: 20px;
    top: 20px;
    display:none;
}

.cover-page-container, .page-container {
    height: auto !important;
}

.page-title-container {
    margin-bottom: 20px;
}

// Only Screen
.preview-container {
    display:none;
}
.screen_display {
    display:block !important;
}



@media print {
    .preview-container {
        display:block !important;
        position: default;
    }
}
</style>