<template>
  <div class="page-title-container">
    <!-- <div class="editor-options">
      <button class="btn btn-default mr-2"><i class="fas fa-heading"></i></button>
      <button class="btn btn-default mr-2"><i class="fas fa-bold"></i></button>
    </div> -->
    <div class="cover-page-container overflow-y-scroll h-100vh">
      <div class="cover-page d-flex flex-column">
          <p type="text" class="form-control text-center dedication_title mb-2" placeholder="Dedication Title" @blur="updateDedicationTitle" :value="dedication_title" contenteditable="true">{{dedication_title}}</p>
          <p type="text" class="form-control text-center dedication_text flex-grow-1" placeholder="Dedication Text" @blur="updateDedicationText" :value="dedication_text" contenteditable="true">{{dedication_text}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  computed: {
    ...mapState({
      dedication_title: (state) => state.data.dedication_title,
      dedication_text:(state) => state.data.dedication_text,
    })
  },
  methods: {
    updateDedicationTitle(e){
      this.$store.commit('updateDedicationTitle', {dedication_title: e.target.innerText })
    },
    updateDedicationText(e){
      this.$store.commit('updateDedicationText', {dedication_text: e.target.innerText })
    },
  }
}
</script>

<style lang="scss" scoped>
  
  .form-control {
    border-width: 0;

    &.book_title {
      font-size: 48px;
    }
    &.author_name {
      font-size: 24px;
    }
  }

</style>