<template>
  <div class="page-title-container">
    <!-- <div class="editor-options">
      <button class="btn btn-default mr-2"><i class="fas fa-heading"></i></button>
      <button class="btn btn-default mr-2"><i class="fas fa-bold"></i></button>
    </div> -->
    <div class="cover-page-container overflow-y-scroll h-100vh">
      <div class="cover-page">
          <input type="file" name="cover_image" id="cover_image" ref="back_cover_image" @change="updateBackCoverImage">
          <div v-if="back_cover_image === null" class="choose_image no-print" title="Upload Image" @click="$refs.back_cover_image.click()">
            <p><i class="fas fa-3x fa-file-image"></i></p>
            <p>Upload a Cover Image</p>
          </div>
          <div v-else class="preview-image text-center">
            <i class="fas fa-2x fa-times-circle no-print" @click="updateBackCoverImage"></i>
            <img :src="back_cover_image" alt="" :class="{'d-none': back_cover_image === null}" class="cover_image">
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  computed: {
    ...mapState({
      back_cover_image:(state) => state.data.back_cover_image,
    })
  },
  methods: {
    updateBackCoverImage(e){
      // console.log(e.target.files);
      this.$store.commit('updateBackCoverImage', {back_cover_image: e.target.files !== undefined ? e.target.files : [] })
    }
  }
}
</script>

<style lang="scss" scoped>
  
  .form-control {
    border-width: 0;

    &.book_title {
      font-size: 48px;
    }
    &.author_name {
      font-size: 24px;
    }
  }

</style>