<template>
    <div>
        <!-- Image Upload Modal -->
        <div class="modal fade" id="ImageModal" tabindex="-1" role="dialog" aria-labelledby="modelTitleId" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">Modal title</h5>
                            <button type="button" @click="clearAndCloseModal" class="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                    </div>
                    <div class="modal-body">
                        <input type="file" name="modal_image" id="modal_image" ref="modal_image" @change="updateModalImage">
                        <div v-if="modal_image === null" class="choose_image" title="Upload Image" @click="$refs.modal_image.click()">
                            <p><i class="fas fa-3x fa-file-image"></i></p>
                            <p>Upload an Image</p>
                        </div>
                        <div v-else class="preview-image text-center">
                            <i class="fas fa-2x fa-times-circle" @click="updateModalImage"></i>
                            <img :src="modal_image" alt="" :class="{'d-none': modal_image === null}" class="modal_image">
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" @click="clearAndCloseModal" class="btn btn-secondary" data-dismiss="modal">Close</button>
                        <button type="button" @click="insertImage" class="btn btn-primary">Insert</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            modal_image: null,
        }
    },
    methods: {
        updateModalImage(e){
            // console.log(e.target.files);
            let images = e.target.files !== undefined ? e.target.files : [];

            if (images.length > 0){
                let image = images[0];
                let imageData = URL.createObjectURL(image);

                this.modal_image = imageData;
            } else {
                this.modal_image = null;
            }
        },
        clearAndCloseModal(){
            this.modal_image = null;

            this.closeImageModal();
        },
        insertImage(){
            if (this.modal_image === null){
                return;
            }

            let html = `<div class="chapter-image-container">
                            <img src="${this.modal_image}" />
                            <div class="caption">Image Caption</div>
                        </div>
                        <div>&nbsp;</div>`;

            this.$emit("image_inserted", {html});

            this.modal_image = null;

            this.closeImageModal();
        }
    }
}
</script>

<style scoped>
#modal_image {
    display:none;
}

.choose_image {
    margin-top: 10px;
}

.preview-image {
    margin-top: 10px;
}

img.modal_image {
    width: 100%;
    height: auto;
}
</style>