<template>
  <div>
    <div class="page-title-container">
      <div class="editor-options">
        <button class="btn btn-default mr-2" @click="openImageModal"><i class="fas fa-image"></i></button>
      </div>
      <div class="page-container overflow-y-scroll h-100vh">
        <div class="page" @click="setCaret" ref="page">
            <h3 class="chapter-title" @blur="updateChapterTitle" contenteditable="true">{{ selected_chapter_object.title }}</h3>
            <div class="page-body" @blur="updateChapterContent" ref="page_body" contenteditable="true" v-html="selected_chapter_object.content"></div>
        </div>
      </div>
    </div>
    <ImageModal @image_inserted="insertImage" />
  </div>  
</template>

<script>
import { mapState } from 'vuex'
import ImageModal from '@/components/ImageModal.vue'
export default {
  computed: {
    ...mapState({
      back_cover_image:(state) => state.data.back_cover_image,
      selected_chapter_id:(state) => state.data.selected_chapter,
      selected_chapter_index:(state, getters) => getters.selected_chapter_index,
      selected_chapter_object:(state, getters) => getters.selected_chapter_object
    })
  },
  methods: {
    updateBackCoverImage(e){
      // console.log(e.target.files);
      this.$store.commit('updateBackCoverImage', {back_cover_image: e.target.files !== undefined ? e.target.files : [] })
    },
    setCaret(e){
      // console.log(e.target, this.$refs.page);
      
      if (e.target === this.$refs.page){
          this.$setCaret(this.$refs.page_body, false);
      }
    },

    updateChapterTitle(e){
      this.$store.commit('updateChapterTitle', {title: e.target.innerText, selected_chapter_index: this.selected_chapter_index})
    },
    updateChapterContent(e){
      this.$store.commit('updateChapterContent', {content: e.target.innerHTML, selected_chapter_index: this.selected_chapter_index})
    },
    insertImage(payload){
      console.log(payload);
      this.$refs.page_body.innerHTML += payload.html;
    }
  },
  mounted(){
    if (undefined !== window.jQuery){
      let $ = window.jQuery;

      $(document).on('keypress', (e)=>{
        
        if (e.key === "Enter"){ // Whene enter button is pressed
          let selection;
          if (window.getSelection){
            selection = window.getSelection();
          } else if (document.selection && document.selection.type != "Control"){
            selection = document.selection;
          }

          let anchor_node = selection.anchorNode;
          let $anchor = $(anchor_node);

          while (!$anchor.hasClass('caption')){
            console.log($anchor);
            if ($anchor.hasClass('page-body')){ // Not part of caption
              $anchor = null;
              break;
            }

            // if ($anchor.hasClass('caption')){ // Part of caption
            //   break;
            // }

            $anchor = $anchor.parent();
          }

          if ($anchor !== null && $anchor.hasClass('caption')){
            e.preventDefault();
            e.stopPropagation();
            let $container = $anchor.parent(); // .chapter-image-container
            if ($container.length === 1){
              $container.after(`<div>&nbsp;</div>`);
              this.$setCaret(this.$refs.page_body, false);
            }
          }

          
        }

        
      });
    }
  },
  components: {
    ImageModal
  }
}
</script>

<style lang="scss" scoped>
  
  .form-control {
    border-width: 0;

    &.book_title {
      font-size: 48px;
    }
    &.author_name {
      font-size: 24px;
    }
  }

  .chapter-title, .page-body {
    &:hover {
        box-shadow: 0 0 5px #aaa;
    }

    &:focus {
        outline: none;
        box-shadow: none;
    }
  }
</style>